@use 'base/mixins' as m;
@use 'base/variables' as v;


.no-proyectos {

    a {
        color: v.$indigo;
    }
}

.listado-proyectos {
    list-style: none;
    padding: 0;
    margin-top: 3rem;

    @include m.grid (1, 2rem);
    @include m.tablet {
        @include m.grid(2, 2rem);
    }

    @include m.desktop {
        @include m.grid (3, 2rem);
    }

    .proyecto {
        background-color: lighten(v.$indigo, 5%);
        min-height: 12rem;
        display: flex;
        align-items: center;
        padding: 2rem;
        line-height: 2;
        border-radius: .5rem;
        color: v.$blanco;


        &:hover {
            background-color: darken(v.$indigo, 5%);
            cursor: pointer;
        }
    }

    .btn-eliminar {
        background-color: v.$indigo;
        color: v.$blanco;
        border: none;
        font-size: 1.2rem;
        padding: .5rem 1rem;
        text-transform: uppercase;
        font-weight: 700;
        border-radius: 1rem;
        cursor: pointer;
        margin-bottom: .5rem;
        
        &:hover {
            background-color: darken(v.$indigo, 10%);
            cursor: pointer;
        }
    }
}