@use 'base/variables' as v;

.alerta {
    padding: 1rem 1rem 1rem 2rem;
    text-transform: uppercase;
    font-weight: 900;
    margin: 1rem 0;
    font-size: 1.2rem;
    max-width: 80rem;
    border-radius: .3rem;
   

    &.error {
        background-color: lighten(v.$rojo, 50%);
        border-left: .5rem solid v.$rojo;
        color: v.$rojo;
    }

    &.exito {
        background-color: lighten(v.$verde, 50%);
        border-left: .5rem solid v.$verde;
        color: v.$verde;
    }

}

.swal2-popup {
    font-size: 1.6rem !important;
  }