@use 'base/variables' as v;
@use 'base/mixins' as m;


.dark-mode {
    $grisOscuro: #2c2c3b;
    body {
        background-color: $grisOscuro;
    }

    .tagline, 
    .formulario .campo label,
    .login .acciones a,
    .crear .acciones a,
    .olvide .acciones a,
    .nombre-pagina, {
        color: v.$blanco;
    }

    .descripcion-pagina,
    .formulario .campo input {
        color: v.$grisClaro;
    }

    .formulario .campo input {
        background-color: lighten($grisOscuro, 15%);
        &::placeholder {
            color: darken(v.$grisClaro, 25%);
        }
    }

    .alerta.error {
        background-color: v.$blanco;
    }

    .barra-mobile,
    .barra {
        background-color: darken($grisOscuro, 7%);
        .dark-mode-btn {
            color: v.$negro;
        }
    }

    .barra {
        p {
            color: v.$naranja;
            
            span {
                color: v.$grisClaro;
            }
        }

       .dark-mode-boton {
        color: v.$naranja;
        filter: invert(100%);
       }
        
        .cerrar-sesion {
            @include m.boton(v.$naranja, v.$blanco);
            padding: 1rem;
            font-size: 1.2rem;
        }
    }

    .sidebar {
        @include m.tablet {
                background-color: darken($grisOscuro, 5%);
        }

        .sidebar-nav a {
            &.activo,
            &:hover {
                background-color: v.$naranja;
            }
        }
    }


    .filtros {
        background-color: lighten($grisOscuro, 10%);
    }

    .dashboard {
        background-color: $grisOscuro;

        a {
            color: v.$naranja;
        }
        h2,
        label {
            color: v.$blanco;
        }

        input[type="radio"] {
            accent-color: v.$naranja;
        }

        .formulario input:not([type="submit"]) {
            background-color: lighten($grisOscuro, 10%);
            color: v.$blanco;
            &::placeholder {
                color: darken(v.$grisClaro, 25%);
            }
        }
    }

    .dashboard .enlace {
        &:hover {
            color: v.$blanco;
        }
    }

    .listado-proyectos .proyecto {
        background-color: lighten($grisOscuro, 20%);
        &:hover {
            background-color: lighten($grisOscuro, 10%);
        }
    }

    .listado-tareas .tarea {
        background-color: lighten($grisOscuro, 10%);
        border: none;
        border-radius: .5rem;

        .puntero {
            &:hover {
                color: v.$grisOscuro;
            }
        }

        p {
            color: v.$grisClaro;
        }
    }

    .no-tareas {
        color: v.$blanco;
    }

    .modal .formulario{
        background-color: lighten($grisOscuro, 10%);
        input[type="text"] {
            background-color: lighten($grisOscuro, 30%);
            border: none;
            color: v.$blanco;

            &::placeholder {
                color: darken(v.$grisClaro, 10%);
            }
        }
        legend {
            color: v.$grisClaro;
        }
    }

  
}