@use 'base/mixins' as m;
@use 'base/variables' as v;


.dashboard {
    background-color: v.$grisClaro;

    @include m.tablet {
        display: flex;
        height: 100vh;

    }

    .principal {
        flex: 1;
        @include m.tablet {
            overflow: scroll;
            overflow-x: hidden;
        }
    }

    .contenido {
        padding: 2rem;

        .nombre-pagina {
            font-size: 2.8rem;
            text-align: center;
        }
    }

    .formulario {
        input:not([type="submit"]) {
            background-color: v.$blanco;
        }

        input[type="submit"] {
            @include m.boton(v.$indigo, v.$blanco);
            padding: 1.2rem;
            text-transform: uppercase;
            font-size: 1.4rem;
        }

        .submit-crear-proyecto {
           @include m.tablet {
               position: relative;
               left: 40%;
           }
        }
    }

    .enlace {
        color: v.$blanco;
        background-color: v.$indigo;
        padding: 1rem;
        border-radius: .5rem;
        font-size: 1.4rem;
        font-weight: bold;
        &:hover {
            color: darken(v.$indigo, 20%);
        }
    }
}