@use 'base/mixins' as m;
@use 'base/variables' as v;

.login {
    margin-top: 5rem;
    
    .uptask {
        @include m.textoDegradado(v.$indigo);
    }

    input[type="submit"] {
        @include m.boton (v.$indigo, v.$blanco);
        @include m.tablet {
            position: relative;
            left: 40%;
        }
    }

    .acciones {
        a {
            color: v.$indigo;
        }
    }
}